<template>
  <v-layout justify-center mt-5>
    <v-flex xs12 sm6>
      <v-card>
        <v-layout row fill-height align-center justify-center>
          <v-flex xs6>
            <v-layout fill-height>
              <v-flex xs12 align-end flexbox pa-3>
                <h2>{{ new Date().toLocaleString() }}</h2>
                <v-select
                  :items="location"
                  v-model="city"
                  label="지역"
                  :item-value="item=>{return item.code}"
                  :item-text="item=>{return item.title}"
                ></v-select>
                <p class="headline">온도 - {{weather.temp}}℃</p>
                <p class="headline">습도 - {{weather.humidity}}%</p>
                <p class="headline">바람 - {{weather.wind_speed}}m/s</p>
              </v-flex>
            </v-layout>
          </v-flex>
          <v-flex xs6>
            <!-- <img :src="weather.icon" width="100" height="100" alt=""> -->
            <v-icon style="width:100px; height:100px; font-size:6rem;" :class="weather.icon"></v-icon>
          </v-flex>
        </v-layout>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import $ from 'jquery';
import axios from "axios";
export default {
  data: () => ({
    card: { title: 'Pre-fab homes', src: 'https://cdn.vuetifyjs.com/images/cards/house.jpg', flex: 12, icon: 'cloud' },
    city:'Seoul',
    apikey: '8ea82574a10697f4842bd8f29bff73fa',
    weather: {},
    location: [
      {code:'Seoul',              title:'서울'},
      {code:'Gyeonggi-do',        title:'경기도'},
      {code:'Gangwon-do',         title:'강원도'},
      {code:'Chungcheongnam-do',  title:'충청남도'},
      {code:'Chungcheongbuk-do',  title:'충청북도'},
      {code:'Jeollabuk-do',       title:'전라북도'},
      {code:'Jeollanam-do',       title:'전라남도'},
      {code:'Gyeongsangbuk-do',   title:'경상북도'},
      {code:'Gyeongsangnam-do',   title:'경상남도'},
      {code:'Jeju-do',            title:'제주도'},
      {code:'Incheon',            title:'인천'},
      {code:'Daejeon',            title:'대전'},
      {code:'Daegu',              title:'대구'},
      {code:'Gwangju',            title:'광주'},
      {code:'Ulsan',              title:'울산'},
      {code:'Busan',              title:'부산'},
    ],

    // time 
    timeCounter : 60, // 1분에 한번씩 호출
  }),
  computed: {
  },
  watch:{
    city(val){
      this.getWeather();
    }
  },
  created(){
    // this.start();
  },
  mounted(){
    this.getWeather();
    this.$store.dispatch('route','/11_openWeatherMap_iocatcher');
  },
  methods:{
    start(){
			// 1초에 한번씩 start 호출
			this.polling = setInterval( () =>{
        this.timeCounter-- //1찍 감소
				if (this.timeCounter <= 0) this.getWeather(); // 0이되는 순간 this.getWeather를 호출한다.
			},1000)
    },
    getWeather(){
      this.timeCounter = 60;
      let param = [
        "/?city="+this.city,
        "apikey="+this.apikey
      ]
      let Params = param.join('&');
      axios.get("http://test.daialab.co.kr:2001/get_weather_info"+Params)
      .then( (resp) => {
        console.log("resp",resp);
        this.weather = resp.data;
      })
      .catch(e => {
          alert(e);
      });
    },
    weatherIcon(value){
      let type = value.substring(0,2);
      var icon = null;
      // console.log(type);
      switch ( type ) {
        case '01':
          icon = 'wi wi-day-sunny';
          break;
        case '02':
          icon = 'wi wi-day-cloudy';
          break;
        case '03':
          icon = 'wi wi-cloud';
          break;
        case '04':
          icon = 'wi wi-cloudy';
          break;
        case '09':
          icon = 'wi wi-rain';
          break;
        case '10':
          icon = 'wi wi-showers';
          break;
        case '11':
          icon = 'wi wi-thunderstorm';
          break;
        case '13':
          icon = 'wi wi-snow';
          break;
        case '50':
          icon = 'wi wi-sprinkle';
          break;
        default:
          icon = 'wi wi-na';
      }
      return icon;
    }
  },
  beforeDestroy() {
		clearInterval(this.polling); // clearInterval은 setInterval을 멈추게 한다. 페이지를 나갈때 setInterval을 멈추게 한다.
	}
}

</script>
